import React, { useEffect, useState } from "react";
import axios from "axios";
import metamaskicon from "../assets/metamask.png";

const ConnectBtn = () => {
  const [address, setAddress] = useState("");
  const [result, setResult] = useState(0);
  const [tbladdresses, setTblAddresses] = useState([]);
  const [tblcounts, setTblCounts] = useState([]);
  const [flag, setFlag] = useState("");

  const current = new Date();
  const date = `${current.getDate()}/${
    current.getMonth() + 1
  }/${current.getFullYear()}`;

  const SaveScore = () => {
    setTimeout(function () {
      setFlag("flag");
    }, 0);
    setTimeout(function () {
      setFlag("");
    }, 5000);

    setResult(document.getElementById("game_mark").value);

    axios
      .post("/api/saveScore", {
        score: document.getElementById("game_mark").value,
        address: address,
        date: date,
      })
      .then((res) => {
        console.log(res);
      })
      .catch((err) => {
        console.log(err);
      });
    axios
      .post("/api/getCount", { date: date })
      .then((res) => {
        setTblAddresses(res.data.addresses);
        setTblCounts(res.data.counts);
      })
      .catch((err) => {
        console.log(err);
      });
  };
  const handleConnect = () => {
    const { ethereum } = window;

    if (ethereum) {
      window.ethereum
        .request({ method: "eth_requestAccounts" })
        .then((accs) => {
          if (accs.length) {
            setAddress(accs[0]);

            window.sessionStorage.setItem("connect", "1");

            axios
              .post("/api/saveAddress", {
                address: accs[0],
                dater: date,
              })
              .then((res) => {
                console.log(res);
              })
              .catch((err) => {
                console.log(err);
              });
            axios
              .post("/api/getCount", { date: date })
              .then((res) => {
                setTblAddresses(res.data.addresses);
                setTblCounts(res.data.counts);
              })
              .catch((err) => {
                console.log(err);
              });
          }
        });
    }
  };

  useEffect(() => {
    axios
      .post("/api/getCount", { date: date })
      .then((res) => {
        setTblAddresses(res.data.addresses);
        setTblCounts(res.data.counts);
      })
      .catch((err) => {
        console.log(err);
      });

    axios
      .post("/api/deleteCount", { date: date })
      .then((res) => {
        console.log(res);
      })
      .catch((err) => {
        console.log(err);
      });
  }, []);

  const getRestart = () => {
    if (document.getElementById("game_restart").value == "save") {
      SaveScore();

      document.getElementById("game_restart").value = "";
    }
  };
  setInterval(getRestart, 1);
  return (
    <>
      <button
        style={{
          
          backgroundColor: "transparent",
          padding: "10px 30px",
          borderColor: "blue",
          borderRadius: "20px",
          fontSize: "20px",
          color: "white",
          marginTop:"15%",
          

       

        }}
        className="connectbtn"
        onClick={handleConnect}
      >
        <img
          alt={metamaskicon}
          src={metamaskicon}
          width={20}
          height={20}
          style={{ marginRight: 10 }}
        />{" "}
        {address ? (
          <span>{address.slice(0, 5) + "..." + address.slice(-2)}</span>
        ) : (
          <span>CONNECT</span>
        )}{" "}
      </button>

      {/* <button
        style={{
          backgroundColor: "transparent",
          padding: "10px 30px",
          borderColor: "blue",
          borderRadius: "20px",
          fontSize: "20px",
          color: "white",
          id: "upload",
          display: "none",
        }}
        disabled={address === "" || flag === "flag"}
        onClick={SaveScore}
      >
        Upload Score
      </button> */}
      <table
      style={{width:"50%",marginTop:"80px",marginLeft:"25%"}}>
        <thead>
          <tr>
            {/* <th>#</th> */}
            <th>Address</th>
            <th>Counts</th>
          </tr>
        </thead>
        <tbody>
          <td >
            {tbladdresses.map((itemAddress) => (
              <tr>{itemAddress.slice(0, 5) + "..." + itemAddress.slice(-3)}</tr>
            ))}
          </td>
          <td>
            {tblcounts.map((itemCounts) => (
              <tr>{itemCounts}</tr>
            ))}
          </td>
        </tbody>
      </table>
    </>
  );
};
export default ConnectBtn;
